import React, { useState } from 'react';
/*import React, { useState, useEffect } from 'react';*/
import { Link } from 'react-router-dom';
import './NavBar.css';
/*import { Button } from './Button';*/



function NavBar() {
    const [click, setClick]= useState(false);
    /*const [button, setButton] = useState(true);*/

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () =>setClick(false);

    /*const showButton= () => {
        if(window.innerWidth <= 960){
            setButton(false)
        } else {
            setButton(true);
        }
    };

    useEffect(() => {
        showButton();
      }, []);

      {button && <Button buttonStyle='btn--outline' >HOME</Button>}
    window.addEventListener('resize', showButton);*/

  return (
    <>
    <nav className='navbar'>
        <div className='navbar-container'>
            <Link to="/" className='navbar-logo' onClick={closeMobileMenu}>
            <i class="fas fa-palette"></i>ART <i class="fa fa-paint-brush" aria-hidden="true"></i>
            </Link>
            <div className='menu-icon' onClick={handleClick}>
                <i className={click ? 'fas fa-times' : 'fas fa-bars'}/>
            </div>
            <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                <li className='nav-item'>
                    <Link to='/about' className='nav-links' onClick={closeMobileMenu}>
                        ABOUT
                    </Link>
                </li>
                <li className='nav-item'>
                    <Link to='/arts' className='nav-links' onClick={closeMobileMenu}>
                        ARTS
                    </Link>
                </li>
                <li className='nav-item'>
                    <Link to='/contact' className='nav-links' onClick={closeMobileMenu}>
                        CONTACTS
                    </Link>
                </li>
                <li>
                <Link to="/" className='navbar-logo nav-links' onClick={closeMobileMenu}>
                HOME
            </Link>
            </li>
          </ul>
          
        </div>
    </nav>
    </>
  );
}

export default NavBar;